'use strict';

app.headerLocation = {
	controlElement: '.js-header-location-control',
	arrowElement: '.js-header-location-arrow',
	dropElement: '.js-header-location-drop',
	init: function init() {
		var _this = this;

		app.common.initScript('jquery.switch-class', 'switchClass', function () {
			if ($(_this.controlElement).length) {
				_this.runToggleHeaderLocation();
			}
		});
	},
	runToggleHeaderLocation: function runToggleHeaderLocation() {
		var self = this;
		$(self.controlElement).switchClass({
			removeExisting: true,
			switchClassTo: $(self.arrowElement).add(self.dropElement),
			modifiers: {
				activeClass: 'drop-is-open'
			}
		});
	}
};