'use strict';

app.headerSearch = {
	controlElement: '.js-header-search-control',
	closeElement: '.js-header-search-close',
	panelElement: '.js-header-search-panel',
	inputElement: '.js-header-search-input',
	init: function init() {
		var _this = this;

		app.common.initScript('jquery.switch-class', 'switchClass', function () {
			if ($(_this.controlElement).length) {
				_this.runHeaderSearch();
			}
		});
	},
	runHeaderSearch: function runHeaderSearch() {
		var self = this;
		var $input = $(self.inputElement);
		$(self.controlElement).switchClass({
			removeExisting: true,
			switchClassTo: $(self.panelElement).add(self.wrapElement),
			removeEl: $(self.closeElement),
			preventRemoveClass: 'js-header-search-prevent-hide',
			modifiers: {
				activeClass: 'header-search-panel-is-open'
			},
			afterAdd: function afterAdd() {
				setTimeout(function () {
					$input.focus();
				}, 50);
			},
			afterRemove: function afterRemove() {
				setTimeout(function () {
					$input.blur();
				}, 50);
			}
		});
	}
};