'use strict';

app.headerPhones = {
	controlElement: '.js-header-phones-control',
	arrowElement: '.js-header-phones-arrow',
	dropElement: '.js-header-phones-drop',
	init: function init() {
		var _this = this;

		app.common.initScript('jquery.switch-class', 'switchClass', function () {
			if ($(_this.controlElement).length) {
				_this.runToggleHeaderPhones();
			}
		});
	},
	runToggleHeaderPhones: function runToggleHeaderPhones() {
		var self = this;
		$(self.controlElement).switchClass({
			removeExisting: true,
			switchClassTo: $(self.arrowElement).add(self.dropElement),
			modifiers: {
				activeClass: 'drop-is-open'
			}
		});
	}
};