'use strict';

app.common = {
	name: 'common',
	description: 'application support functions',
	init: function init() {
		this.initScript('svg4everybody.min', 'svg4everybody', function () {
			svg4everybody();
		});
	},
	addScriptFile: function addScriptFile(nameFile, callback) {
		var body = document.getElementsByTagName('body')[0];
		var script = document.createElement('script');
		script.onload = function () {
			return callback && callback();
		};
		script.src = app.pathToLibsFiles + '/js/' + nameFile + '.js';
		body.appendChild(script);
	},
	addStyleFile: function addStyleFile(nameFile) {
		var appCss = document.getElementById('app-css');
		var link = document.createElement('link');
		link.rel = 'stylesheet';
		link.href = app.pathToLibsFiles + '/css/' + nameFile + '.css';
		appCss.before(link);
	},
	initScript: function initScript(nameFile, nameLib, callback) {
		var hasLibs = null;
		$('script').each(function () {
			var str = $(this).attr('src');
			if (str) {
				if (str.indexOf(nameFile) >= 0) {
					hasLibs = true;
				}
			}
		});
		if (!hasLibs) {
			this.addScriptFile(nameFile, callback);
		} else {
			var timerId = setInterval(function () {
				if ($()[nameLib] || window[nameLib]) {
					callback();
					clearInterval(timerId);
				}
			}, 1);
		}
	},
	initStyle: function initStyle(nameFile) {
		var hasLibs = null;
		$('link').each(function () {
			var str = $(this).attr('href');
			if (str) {
				if (str.indexOf(nameFile) >= 0) {
					hasLibs = true;
				}
			}
		});
		if (!hasLibs) {
			this.addStyleFile(nameFile);
		}
	},
	initLazyLoad: function initLazyLoad() {
		app.common.initScript('lazyload.min', 'LazyLoad', function () {
			new LazyLoad({
				threshold: 50,
				elements_selector: '.lazy-load'
			});
		});
	},
	initTouchEvents: function initTouchEvents() {
		app.touchevents = 'ontouchstart' in document.documentElement;
	},
	toggleMobMenu: function toggleMobMenu() {
		app.common.initScript('jquery.switch-class', 'switchClass', function () {
			var $controlElement = $('.js-mob-menu-control');
			var $html = $('html');
			if ($controlElement.length) {
				$controlElement.switchClass({
					removeExisting: true,
					switchClassTo: $('.js-mob-menu').add('.js-mob-menu-overlay'),
					removeEl: $('.js-mob-menu-close').add('.js-mob-menu-overlay'),
					cssScrollFixed: true,
					preventRemoveClass: 'js-mob-menu-prevent-hide',
					modifiers: {
						activeClass: 'mob-menu-is-open'
					},
					afterAdd: function afterAdd() {
						$html.addClass('open-only-mob');
					},
					afterRemove: function afterRemove() {
						$html.removeClass('open-only-mob');
					}
				});
			}
		});
	}
};