'use strict';

app.navigation = {
	navElement: '.js-nav',
	init: function init() {
		var _this = this;

		app.common.initScript('jquery.nav', 'switchClass', function () {
			if ($(_this.navElement).length) {
				_this.runNavigation();
			}
		});
	},
	runNavigation: function runNavigation() {
		$(this.navElement).nav({
			// Elements
			item: '.js-nav-li',
			drop: '.js-nav-drop',
			arrow: '.js-nav-arrow',
			// Additional settings
			arrowEnable: true,
			submenuPosition: false
		});
	}
};